/* ============================================= */
/*              Navigation                       */
/* ============================================= */

.nav {
    padding-left: 0;
    flex-basis: $max;
    display: flex;
    flex-direction: column;
    &-link {
      text-align: center;
      border-radius: 5px;
      padding: 10px 0;
      display: block;
      width: $max;
      transition: 
        background-color .2s ease-in-out, 
        color .2s ease-in-out;
    }
    &-link:hover {
      color: $white;
      background-color: $color-primary;
    }
  }
  
